.step-form {
  margin: 10px 20px 0;
  padding-bottom: 20px;
  border-bottom: 1px dashed #EEE; }
  .step-form .help {
    font-size: 14px;
    color: #555;
    margin-bottom: 10px; }
  @media (max-width: 800px) {
    .step-form {
      margin: 5px 10px 0;
      padding-bottom: 10px; } }
  .step-form .input-group-addon {
    border-width: 2px;
    border-color: #BDC3C7;
    transition: border .25s linear, color .25s linear, background-color .25s linear; }
  .step-form .has-error .input-group-addon {
    border-color: #E74C3C;
    color: #b61b17; }
  .step-form .row:last-child .form-group {
    margin-bottom: 0; }
  .step-form .material-group {
    margin-top: 10px; }
    .step-form .material-group label {
      font-size: 12px;
      color: #00796B;
      text-transform: uppercase;
      display: block;
      width: 100%;
      margin-bottom: 0;
      line-height: 18px;
      transition: color linear .25s; }
    .step-form .material-group input,
    .step-form .material-group select {
      font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
      background: none;
      border: 0;
      border-bottom: 1px solid #eeeeee;
      border-radius: 0;
      display: block;
      width: 100%;
      outline: none;
      color: #888;
      font-size: 14px;
      line-height: 24px;
      transition: color linear .25s, border-color linear .25s; }
      @media (max-width: 800px) {
        .step-form .material-group input,
        .step-form .material-group select {
          font-size: 11.9px; } }
      .step-form .material-group input:focus, .step-form .material-group input:active,
      .step-form .material-group select:focus,
      .step-form .material-group select:active {
        border-color: #00796B;
        color: #00796B; }
    .step-form .material-group select {
      -webkit-appearance: none;
      -moz-appearance: none;
      padding: 1px 0;
      height: 27px; }
    .step-form .material-group.has-error label {
      color: #b61b17; }
    .step-form .material-group.has-error input,
    .step-form .material-group.has-error select {
      border-color: #E53935;
      color: #b61b17; }
    .step-form .material-group .btn-group button {
      font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
      font-size: 14px;
      color: #a2a2a2;
      outline: none;
      padding: 6px 0;
      text-align: left;
      transition: linear .25s;
      margin-right: 10px;
      border-radius: 0;
      background: none; }
      .step-form .material-group .btn-group button:hover, .step-form .material-group .btn-group button:focus, .step-form .material-group .btn-group button:active, .step-form .material-group .btn-group button.active {
        color: #00796B; }
      .step-form .material-group .btn-group button:last-child {
        margin-right: 0; }
      @media (max-width: 800px) {
        .step-form .material-group .btn-group button {
          font-size: 11.9px; } }
