.step-selector {
  background: #FFF;
  border-radius: 3px;
  position: relative;
  max-width: 1024px;
  margin: 0 auto 20px;
  border-top: 4px solid #1976D2; }
  @media (max-width: 1024px) {
    .step-selector {
      border-radius: 0; } }
  .step-selector .body {
    padding: 20px; }
    @media (max-width: 800px) {
