header {
  padding: 30px 30px 0;
  background: #00796B; }
  @media (max-width: 800px) {
    header {
      padding: 15px 15px 0; } }
  header h1 {
    margin: 0 auto;
    padding: 0;
    width: 325px;
    height: 86px;
    background: url("/images/logo_with_strap.png") top left no-repeat;
    background-size: 100%; }
    header h1 span {
      display: none; }
    @media (max-width: 800px) {
      header h1 {
        width: 250px;
        height: 66px; } }
    @media (max-width: 360px) {
      header h1 {
        width: 160px;
        height: 42px; } }
  header nav {
    display: table;
    margin: 10px auto 0; }
    header nav a {
      font-family: "Lato", Arial, sans-serif;
      display: inline-block;
      text-decoration: none;
      padding: 10px 15px;
      color: #B2DFDB;
      transition: .25s linear;
      font-size: 14px;
      text-align: center;
      text-transform: uppercase;
      text-shadow: 1px 1px 2px #00463e; }
      header nav a:hover, header nav a:focus, header nav a:active, header nav a.active {
        color: #FFF;
        padding-bottom: 8px;
        border-bottom: 2px solid #F3F3F3; }
      header nav a.reset {
        cursor: pointer;
        border-left: 1px solid rgba(255, 255, 255, 0.1); }
