.notfound {
  background: #cd1e1a;
  position: relative;
  box-shadow: 0 0 5px #e6e6e6;
  border-radius: 3px;
  max-width: 1024px;
  margin: 0 auto 20px;
  font-size: 14px;
  padding: 20px;
  color: #FFF;
  text-align: center; }
  @media (max-width: 800px) {
    .notfound {
      border-radius: 0; } }
  .notfound h1 {
    font-weight: normal;
    margin: 0;
    font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
    font-size: 150%; }
    .notfound h1 span {
      font-size: 300%; }
  .notfound a {
    color: #FFF; }
    .notfound a:active, .notfound a:hover, .notfound a:focus {
      color: #FFEBEE; }
