.intro {
  background: #FFF;
  position: relative;
  box-shadow: 0 0 5px #e6e6e6;
  border-radius: 3px;
  max-width: 1024px;
  margin: 0 auto 20px;
  border-top: 4px solid #1976D2;
  transition: border-color .2s linear;
  font-size: 14px;
  padding: 10px 20px 20px;
  color: #555;
  line-height: 1.7; }
  @media (max-width: 800px) {
    .intro {
      padding: 5px 10px 10px;
      border-radius: 0; } }
  .intro h1 {
    color: #1976D2;
    text-align: center;
    margin-top: 0;
    font-weight: normal;
    font-size: 25px; }

.precomposed {
  background: #FFF;
  position: relative;
  box-shadow: 0 0 5px #e6e6e6;
  border-radius: 3px;
  max-width: 1024px;
  margin: 0 auto 20px;
  border-top: 4px solid #512DA8;
  transition: border-color .2s linear;
  font-size: 14px;
  padding: 20px;
  color: #555;
  line-height: 1.7; }
  @media (max-width: 800px) {
    .precomposed {
      padding: 10px;
      border-radius: 0; } }
  .precomposed h3 {
    color: #512DA8;
    text-align: center;
    margin-top: 0;
    font-weight: normal;
    font-size: 25px; }
  .precomposed a {
    background: #512DA8;
    color: #FFF;
    display: inline-block;
    font-family: Lato, Helvetica, Arial, sans-serif;
    font-size: 14px;
    outline: none;
    padding: 4px 8px;
    text-align: left;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 3px; }
    .precomposed a:hover, .precomposed a:focus, .precomposed a:active {
      background: #673dcb; }
