.footer {
  background: #313131;
  color: #888;
  font-size: 14px;
  line-height: 1.7; }
  .footer h4 {
    color: #AAA;
    text-transform: uppercase;
    margin: 20px 0; }
  .footer a {
    color: #888; }
    .footer a:hover, .footer a:active, .footer a:focus {
      color: #AAA; }
  .footer .container-fluid {
    max-width: 1024px;
    padding: 10px 20px 20px;
    margin: 0 auto; }
    @media (max-width: 800px) {
      .footer .container-fluid {
        padding: 10px; } }
  .footer ul {
    margin: 0;
    padding: 0;
    list-style-type: none; }
