@keyframes slide-in {
  0% {
    display: inline-block;
    transform: translateY(-100%); }
  100% {
    display: inline-block;
    transform: translateY(0%); } }

@keyframes slide-out {
  0% {
    transform: translateY(0%); }
  99% {
    opacity: 0;
    transform: translateY(-100%); }
  100% {
    opacity: 0;
    height: 0;
    transform: translateY(-100%); } }

.toast {
  background: rgba(0, 121, 107, 0.8);
  display: inline-block;
  font-size: 14px;
  color: #FFF;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 50%;
  right: 50%;
  width: 250px;
  text-align: center;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  margin-left: -125px;
  z-index: 100000; }
  .toast.error {
    background: rgba(182, 27, 23, 0.8); }
  .toast.slide-in {
    animation: slide-in .25s forwards;
    animation-timing-function: ease-out; }
  .toast.slide-out {
    animation: slide-out .25s forwards;
    animation-timing-function: ease-out; }
