html {
  margin: 0;
  padding: 0; }

body {
  font-family: "Lato", Arial, sans-serif;
  background: #313131;
  margin: 0;
  padding: 0; }

a {
  color: #00796B;
  text-decoration: none;
  transition: .25s linear; }
  a:active, a:hover, a:focus {
    color: #00463e; }

.pull-left {
  float: left; }

.pull-right {
  float: right; }

button {
  cursor: pointer;
  border: 0;
  outline: none;
  font-size: 14px;
  transition: .25s linear;
  padding: 10px 20px; }

.content {
  background: #F3F3F3;
  padding: 20px 0; }
  @media (max-width: 800px) {
    .content {
      padding: 14px 0; } }

.about {
  background: #006054;
  position: relative;
  box-shadow: 0 0 5px #e6e6e6;
  border-radius: 3px;
  max-width: 1024px;
  margin: 0 auto 20px;
  font-size: 14px;
  padding: 20px;
  color: #FFF;
  text-align: center; }
  @media (max-width: 800px) {
    .about {
      border-radius: 0; } }
  .about h1 {
    font-weight: normal;
    margin: 0; }
  .about .version {
    padding: 5px 0;
    margin-bottom: 20px;
    color: #B2DFDB; }
  .about .author {
    margin-bottom: 10px; }
  .about .github {
    padding: 5px 0; }
    .about .github a {
      color: #B2DFDB; }
      .about .github a:active, .about .github a:hover, .about .github a:focus {
        color: #6ac1b9; }
      .about .github a span {
        font-size: 35px; }
  .about a {
    color: #FFF; }
    .about a:active, .about a:hover, .about a:focus {
      color: #B2DFDB; }

.page {
  background: #FFF;
  position: relative;
  box-shadow: 0 0 5px #e6e6e6;
  border-radius: 3px;
  max-width: 1024px;
  margin: 0 auto 20px;
  border-top: 4px solid #00796B;
  transition: border-color .2s linear;
  font-size: 14px;
  padding: 20px;
  color: #555;
  line-height: 1.7; }
  @media (max-width: 800px) {
    .page {
      padding: 10px;
      border-radius: 0; } }
  .page ol.contents li {
    padding: 5px 0; }
  .page h1 {
    margin: 0 0 20px;
    font-weight: normal;
    color: #00796B; }
  .page h4 {
    font-size: 22px;
    font-weight: normal;
    margin: 20px 0;
    padding: 0;
    color: #00796B; }
    .page h4:first-child {
      margin-top: 0; }
    .page h4 span {
      color: #00796B; }
  .page h5 {
    font-size: 18px;
    font-weight: normal;
    margin: 0 0 10px;
    padding: 0;
    color: #00796B; }
  .page .guide-section-content {
    padding-left: 22px; }
  .page ul li {
    padding: 5px 0; }
