.dropzone {
  font-size: 14px;
  text-align: center;
  color: #555;
  padding: 20px;
  line-height: 1.7;
  cursor: pointer;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px; }
  .dropzone .ionicon {
    font-size: 30px;
    line-height: 33.6px; }
  .dropzone small {
    color: #6f6f6f; }
  @media (max-width: 800px) {
    .dropzone {
      padding: 10px 10px 5px;
      font-size: 10.5px; } }
  .dropzone.active {
    background: #E0F2F1; }
  .dropzone.reject {
    background: #FFEBEE; }
  .dropzone .file-success {
    font-size: 14px;
    text-align: center;
    color: #2E7D32;
    padding-top: 10px; }
  .dropzone .file-error {
    font-size: 14px;
    text-align: center;
    color: #b61b17;
    padding-top: 10px; }
  .dropzone .drop-alt {
    line-height: 1.7;
    font-size: 12px; }

.binary {
  max-width: 500px;
  padding: 0 20px;
  margin: 0 auto 20px;
  font-size: 12px;
  text-align: center;
  color: #555; }
  @media (max-width: 800px) {
    .binary {
      padding: 0 10px;
      margin: 0 auto 10px; } }
